// @charset 'utf-8';

.tui-pagination * {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
  overflow: hidden;
}

.tui-pagination {
  height: 28px;
  margin: 14px 0 12px;
  line-height: normal;
  text-align: center;
  font-size: 0;
}

.tui-pagination .tui-page-btn {
  display: inline-block;
  position: relative;
  width: 40px;
  height: 40px;
  padding: 12px 0 6px;
  color: #000;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
  text-decoration: none;
  vertical-align: middle;
  border-radius: 5px;
  transition: color 0.25s ease-in-out, background-color 0.25s ease-in-out,
    opacity 0.25s linear;
}

.tui-pagination .tui-page-btn:hover {
  color: #fff;
  background-color: #ff8000;
  opacity: 0.6;
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
  color: #fff;
  background: #ff6b08;
  cursor: pointer;
}

.tui-pagination .tui-is-selected:hover {
  background-color: #ff6b08;
  opacity: 1;
}

.tui-pagination .tui-first,
.tui-pagination .tui-prev,
.tui-pagination .tui-next,
.tui-pagination .tui-last,
.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  overflow: hidden;
  width: 40px;
  height: 40px;
  padding: 8px;
  font-size: 0;
  line-height: 26px;
  white-space: nowrap;
  font-weight: normal;
  border-radius: 5px;
}

.tui-pagination .tui-ico-first,
.tui-pagination .tui-ico-prev,
.tui-pagination .tui-ico-next,
.tui-pagination .tui-ico-last,
.tui-pagination .tui-ico-ellip {
  background: url(data:image/png;base64,)
    no-repeat 0 0;
  display: inline-block;
  vertical-align: middle;
  font-size: 0px;
}

.tui-pagination .tui-prev-is-ellip,
.tui-pagination .tui-next-is-ellip {
  padding-top: 8px;
  line-height: 1;
}

.tui-pagination .tui-ico-first {
  width: 11px;
  height: 10px;
  background-position: 0px 0px;
}

.tui-pagination .tui-ico-prev {
  width: 6px;
  height: 10px;
  background-position: -26px 0px;
}

.tui-pagination .tui-ico-next {
  width: 6px;
  height: 10px;
  background-position: -13px -24px;
}

.tui-pagination .tui-ico-last {
  width: 11px;
  height: 10px;
  background-position: 0px -12px;
}

.tui-pagination .tui-ico-ellip {
  width: 10px;
  height: 2px;
  margin-top: 16px;
  vertical-align: top;
  background-position: 0px -24px;
}

.tui-pagination .tui-is-disabled:hover {
  background: #fff;
  cursor: pointer;
}

.tui-pagination .tui-is-disabled .tui-ico-prev {
  background-position: -26px -12px;
}

.tui-pagination .tui-is-disabled .tui-ico-first {
  background-position: -13px 0px;
}

.tui-pagination .tui-is-disabled .tui-ico-next {
  background-position: -21px -24px;
}

.tui-pagination .tui-is-disabled .tui-ico-last {
  background-position: -13px -12px;
}

.tui-page-inputs {
  display: inline-block;
  vertical-align: top;
}

.tui-page-inputs .tui-page-input {
  display: inline-block;
  width: 10px;
  height: 12px;
  padding: 8px 0 6px;
  font-size: 12px;
  text-align: center;
  color: #333;
  border: 0;
  outline: none;
}

.tui-page-inputs .tui-page-current {
  color: #4b96e6;
}

.tui-page-separator {
  display: inline-block;
  font-size: 12px;
  height: 12px;
  padding: 8px 0 6px;
  vertical-align: top;
}

// ================================================

.pagination.section {
  padding-top: 20px;
  padding-bottom: 40px;
}

.pagination.section.invisible {
  display: none;
}

.pagination.container {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (max-width: 767px) {
  .pagination.container {
    max-width: 320px;
  }

  .tui-pagination span.tui-first,
  .tui-pagination span.tui-last,
  .tui-pagination span.tui-prev-is-ellip,
  .tui-pagination span.tui-next-is-ellip,
  .tui-pagination .tui-page-btn.tui-first,
  .tui-pagination .tui-page-btn.tui-last,
  .tui-pagination .tui-page-btn.tui-prev-is-ellip,
  .tui-pagination .tui-page-btn.tui-next-is-ellip {
    display: none;
  }
}

@media screen and (min-width: $tablet) {
  .tui-pagination .tui-page-btn:not(:first-child) {
    margin-left: 4px;
  }
}
